import React, { Component } from 'react';
import { Link, NavLink, withRouter } from 'react-router-dom';
import { Badge, UncontrolledDropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import PropTypes from 'prop-types';

import { AppAsideToggler, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import logo from '../../assets/img/brand/mispace3.png'
import sygnet from '../../assets/img/brand/sygnet.svg'
import jwt_decode from 'jwt-decode';
import ChangePassword from '../../views/Settings/ChangePassword'
import UserInfo from '../../constants/credentials'
import './Header.scss'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      property_name: '',
      email: '',
      token: '',
      passwordModalShow: false
    };
  }

  componentDidMount(){
    const token = localStorage.userToken;

    const decoded = jwt_decode(token);
    let expirationDate = decoded.exp;
    var current_time = Date.now() / 1000;
      
    if(expirationDate < current_time){
      localStorage.removeItem('userToken');
      this.props.history.push('/login')
    }
    else{
      this.setState({
        property_name: decoded.property_name,
        email: decoded.email
      })
    }
  }

  monthlyReport(e){
    e.preventDefault()
    this.props.history.push('/general_monthly_report')
  }

  branchMonthlyReport(e){
    e.preventDefault()
    this.props.history.push('/landlord_monthly_report')
  }

  annualReport(e){
    e.preventDefault()
    this.props.history.push('/general_annual_report')
  }

  brandAnnualReport(e){
    e.preventDefault()
    this.props.history.push('/landlord_annual_report')
  }

  brandWithdrawReport(e){
    e.preventDefault()
    this.props.history.push('/landlord_withdraw_report')
  }

  logOut(e){
    e.preventDefault()
    localStorage.removeItem('userToken')
    this.props.history.push('/login')
  }

  render() {

    let passwordModalClose = () => this.setState({passwordModalShow : false});

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;
    const { property_name } = this.state;
    
    if(property_name == null){
      this.props.history.push('/login')
    }else{
    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppNavbarBrand
          full={{ src: logo, width: 150, height: 40, alt: 'Mi-space Logo' }}
          // minimized={{ src: sygnet, width: 30, height: 30, alt: 'CoreUI Logo' }}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg" />

        <Nav className="d-md-down-none" navbar>
          <NavItem className="px-3">
            <NavLink to="/dashboard" className="nav-link" >Dashboard</NavLink>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/bookings" className="nav-link icon-list"> Bookings</Link>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/account_balances" className="nav-link icon-wallet"> Account Balance</Link>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/withdraws" className="nav-link icon-film"> Withdraws Requests</Link>
          </NavItem>
          <UncontrolledDropdown nav direction="down">
            <DropdownToggle nav>
              <NavItem className="px-3">
                <NavLink to="#" className="nav-link icon-note"> Reports</NavLink>
              </NavItem>
            </DropdownToggle>
            <DropdownMenu right>
              {/* <DropdownItem tag="div" className="text-center">Transaction Reports</DropdownItem> */}
              {/* <DropdownItem onClick={this.monthlyReport.bind(this)}><i className="icon-book-open"></i>General Bookings Monthly Report</DropdownItem>
              <DropdownItem onClick={this.annualReport.bind(this)}><i className="icon-book-open"></i>General Bookings Annual Report</DropdownItem> */}
              <DropdownItem onClick={this.branchMonthlyReport.bind(this)}><i className="icon-book-open"></i>Landloard Bookings Monthly Report</DropdownItem>
              <DropdownItem onClick={this.brandAnnualReport.bind(this)}><i className="icon-book-open"></i>Landloard Bookings Annual Report</DropdownItem>
              <DropdownItem divider />
              <DropdownItem onClick={this.brandWithdrawReport.bind(this)}><i className="icon-book-open"></i>Landlord Withdraw Report</DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
          <UncontrolledDropdown nav direction="down">
            <DropdownToggle nav>
              <NavItem className="px-3">
                <NavLink to="#" className="nav-link fa fa-wrench"> Settings</NavLink>
              </NavItem>
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem onClick = {() => this.setState({passwordModalShow : true})}><i className="fa fa-shield"></i> Change Password</DropdownItem>
              <DropdownItem onClick={this.logOut.bind(this)}><i className="fa fa-lock"></i> Logout</DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Nav>
        <Nav className="ml-auto" navbar>
        <UncontrolledDropdown nav direction="down">
            <DropdownToggle nav>
              <NavItem className="d-md-down-none">
                <NavLink to="#" className="nav-link fa fa-home"> {UserInfo.PropertyName} </NavLink>
              </NavItem>
            </DropdownToggle>
            {/* <DropdownMenu right>
              <DropdownItem header tag="div" className="text-center"><strong>Settings</strong></DropdownItem>
              <DropdownItem><i className="fa fa-user"></i> Profile</DropdownItem>
              <DropdownItem><i className="fa fa-wrench"></i> Settings</DropdownItem>
              <DropdownItem divider />
              <DropdownItem><i className="fa fa-shield"></i> Lock Account</DropdownItem>
              <DropdownItem onClick={this.logOut.bind(this)}><i className="fa fa-lock"></i> Logout</DropdownItem>
            </DropdownMenu> */}
          </UncontrolledDropdown>

        </Nav>
        <div className="side-right"></div>
        {/* <AppAsideToggler className="d-md-down-none" /> */}
        {/*<AppAsideToggler className="d-lg-none" mobile />*/}
        <ChangePassword
          show = {this.state.passwordModalShow}
          onHide = {passwordModalClose}/>
      </React.Fragment>
    );
  }}
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default withRouter(DefaultHeader);
