import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
import jwt_decode from 'jwt-decode';
// import Spinner from '../../Loaders/Spinner'
import './Settings.scss'
import Server_connections from '../../constants/constants';

class ChangePassword extends Component {
    constructor(props) {
      super(props);
      this.state = {
        id: '',
        old_password: '',
        new_password: '',
        token: '',
        email: '',
        message: '',
        isLoaded: false
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    componentDidMount(){
        const token = localStorage.userToken;
    
        const decoded = jwt_decode(token);
        this.setState({
            id: decoded.id,
            email: decoded.email
        })
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleUpdateItem(e){
      e.preventDefault();

      const change_password = {   
        old_password: this.state.old_password,
        new_password: this.state.new_password,
        email: this.state.email
      };

      fetch(Server_connections.CHANGE_ADMIN_PASSWORD_URL + `${this.state.id}`,{
        method: 'PUT',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(change_password)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });

    }
    
    render() {
      var { regions, handleUpdateItem} = this.state;
      
        return (
            <Modal {...this.props} size="md" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter"><h5>Change Account Password</h5></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h6><span className='error-msg'>{this.state.message }</span></h6>
                    <FormGroup row className="my-0">
                        <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="old_password" className="lable">Current Password</Label>
                                <Input type="text" name="old_password" placeholder= "Enter Current Password" value={this.state.old_password} onChange={this.onChange}/>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="new_password" className="lable">New Password</Label>
                                <Input type="text" name="new_password" value={this.state.new_password} onChange={this.onChange}
                                placeholder= "Enter New Password">
                                </Input>
                            </FormGroup>
                        </Col>
                    </FormGroup>
                </Modal.Body>
                <Modal.Footer>
                <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Close</Button>
                </div>
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Save</Button>
                </div>
              </Modal.Footer>
            </Modal>
        )
    }
}

export default ChangePassword;
